import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

// ��ȡԭ�Ͷ���push����
const originalPush = VueRouter.prototype.push

// ��ȡԭ�Ͷ���replace����
const originalReplace = VueRouter.prototype.replace

// �޸�ԭ�Ͷ����е�push����
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

// �޸�ԭ�Ͷ����е�replace����
VueRouter.prototype.replace = function replace(location) {
  return originalReplace.call(this, location).catch(err => err)
}

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import(/* webpackChunkName: "about" */ '../views/Home.vue')
  },
  {
    path: '/login',
    name: 'login',
    component: () => import(/* webpackChunkName: "about" */ '../views/login.vue')
  },
  {
    path: '/createAccount',
    name: 'createAccount',
    component: () => import(/* webpackChunkName: "about" */ '../views/createAccount.vue')
  },
  {
    path: '/product',
    name: 'product',
    component: () => import(/* webpackChunkName: "about" */ '../views/product.vue')
  },
  {
    path: '/support',
    name: 'support',
    component: () => import(/* webpackChunkName: "about" */ '../views/support.vue')
  },
  {
    path: '/dashboard',
    name: 'dashboard',
    component: () => import(/* webpackChunkName: "about" */ '../views/dashboard.vue')
  },
  {
    path: '/dashboard/index',
    name: 'dashboardIndex',
    component: () => import(/* webpackChunkName: "about" */ '../views/dashboard/index.vue'),
    children: [
      {
        path: "userlist",
        name: "userList",
        component: () => import(/* webpackChunkName: "about" */ '../views/dashboard/userList.vue'),
      },
      {
        path: "usergroup",
        name: "userGroup",
        component: () => import(/* webpackChunkName: "about" */ '../views/dashboard/userGroup.vue'),
      }
    ]  },
  {
    path: '/policy',
    name: 'policy',
    component: () => import(/* webpackChunkName: "about" */ '../views/policy.vue')
  },
  {
    path: '/loginTo',
    name: 'loginTo',
    component: () => import(/* webpackChunkName: "about" */ '../views/loginTo.vue')
  },
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior () {
    return { x: 0, y: 0 }
  }
})

export default router
